.App{
  min-height: 200vh;
  background-image: linear-gradient(to right, #fa709a 0%, #fee140 100%);
}
.title{
  justify-content: center;
  align-items:center;
  padding: 40px;

}
.search_form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.search_bar{
  width: 50%;
  border: none;
  padding: 10px;
}
.search_button{
  background: #CD5C5C;
  border: none;
  padding: 10px 20px;
  color: white;
}
.recipes{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
